<template lang="html">
  <div class="card-head">
    <div @click="goUrl" class="title-box">
      <span>{{title}}</span>
      <!-- <img src="../../assets/img/personCompanyCard/x05@3x.png" alt=""> -->
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: '个人名片'
    },
    path: {
      type: String,
      default: ''
    }
  },
  methods: {
    goUrl () {
      this.$router.push(this.path)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.card-head {
  position: fixed;;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  background:@whiteColor;
  border-bottom: 1px solid @lineColor;
  height: .88rem;
  line-height: .88rem;
  text-align: center;
  .title-box {
    display: inline-block;
    font-size: .36rem;
    color: @defaultColor;
    img {
      width: .46rem;
      height: .46rem;
      margin-right: .1rem;
      vertical-align: middle;
    }
  }
}
</style>



