<template lang="html">
  <div class="person-company-head" @click="goTo(pathname)">
    <div class="left">
      <img :src="img || pic" alt="">
    </div>
    <div class="right">
      <div class="name">{{text}}</div>
      <div class="mobile"><span>手机号:</span><span>{{mobile}}</span></div>
    </div>
  </div>
</template>

<script>
import pic from '@/assets/img/personCompanyCard/ph2@3x.png'
export default {
  props: {
    img: {
      type: String,
      default: pic
    },
    text: {
      type: String,
      required: true
    },
    mobile: {
      type: String,
      default: '未设置'
    },
    pathname: {
      type: String
    }
  },
  data () {
    return {
      pic
    }
  },
  methods: {
    goTo (pathname) {
      this.$router.push(pathname)
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';
.person-company-head {
  background: @whiteColor;
  padding: 0.22rem;
  display: table;
  .left {
    display: table-cell;
    width: 1.4rem;
    img {
      width: 1.4rem;
      height: 1.6rem;
    }
  }
  .right {
    display: table-cell;
    width: 100%;
    padding-left: .3rem;
    vertical-align: middle;
    line-height: .6rem;
    .name {
      color: @defaultColor;
      font-size: .32rem;
    }
    .mobile {
      color: @lightColor;
      font-size: .32rem;
    }
  }
}
</style>



