<template lang="html">
  <div class="">
    <card-head></card-head>
    <div class="person-company-cnt">
      <person-company-head :text="cardInfo.nickName || '用户昵称'" pathname="/personinfo"
      :mobile="cardInfo.mobile || '未设置'" :img="cardInfo.facePic || ''"></person-company-head>
      <div class="info-box">
        <info-line :icon="icons[2]" pathname="/personrecognize">
          <div class="text-box" >
            <div class="text-left">个人真实性认证</div>
            <div class="text-right">{{status[cardInfo.status]}}</div>
          </div>
        </info-line>
        <info-line :icon="icons[3]" pathname="/specialindustry">
          <div class="text-box" >
            <div class="text-left">特殊行业资格认证</div>
            <div class="text-right">{{status[cardInfo.specialAuthStatus]}}</div>
          </div>
        </info-line>
      </div>
      <div class="info-box">
        <info-line :icon="icons[0]" pathname="/personkeywords">
          <div class="text-box">
            <div class="text-left">添加标签关键字</div>
            <div class="text-right">{{cardInfo.hasTags > 0 ? '已添加':'未添加'}}</div>
          </div>
        </info-line>
        <info-line :icon="icons[1]" pathname="/personkeysort">
          <div class="text-box" >
            <div class="text-left">标签关键词数据</div>
            <div class="text-right">{{cardInfo.hasTags > 0 ? '有数据':'暂无数据'}}</div>
          </div>
        </info-line>
      </div>
    </div>

    <tabbar selected="/personcard"></tabbar>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import cardHead from '@/components/personCompanyCard/cardHead'
import personCompanyHead from '@/components/personCompanyCard/personCompanyHead'
import infoLine from '@/components/common/infoLine'
import addkey from '@/assets/img/personCompanyCard/a02@3x.png'
import datakey from '@/assets/img/personCompanyCard/a03@3x.png'
import sure from '@/assets/img/personCompanyCard/a04@3x.png'
import sperise from '@/assets/img/personCompanyCard/a05@3x.png'
import tabbar from '@/components/minCommon/tabbar'

export default {
  components: {cardHead, personCompanyHead, infoLine, tabbar},
  data () {
    return {
      icons: [sure, datakey, addkey, sperise],
      status: ['未认证', '审核中', '已驳回', '已认证'],
      cardInfo: {}
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      let userId = utils.userId()
      userId && this.ajaxMethod({userId})
    },
    ajaxMethod (data) {
      minutils.IndicatorOpen('加载中...')
      api.getIDCard(data).then((response) => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.cardInfo = result.data
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.person-company-cnt {
  padding-top: .88rem;
}
.info-box {
  margin-top: .3rem;
}
.text-box {
  overflow: hidden;
  .text-left {
    float:left;
    width: 3rem;
    font-size: .32rem;
    color: @defaultColor;
  }
  .text-right {
    float: right;
    color: @lightColor;
    font-size: .28rem;
  }
}
</style>



